.block3 {
	p {
			@include myFont ('Kyiv', normal, 500, 30px, 133%, #F5F5F5);
			margin-top: 210px;
			margin-bottom: 90px;
			margin-left: 8px;
			max-width: 240px;

			@media screen and (min-width:576px) {
					margin-left: 100px;
			}

			@media screen and (min-width:768px) {
					@include myFont ('Kyiv', normal, 500, 60px, 133%, #F5F5F5);
					margin-top: 210px;
					margin-bottom: 125px;
					margin-left: 100px;
					max-width: 480px;
			}

			@media screen and (min-width:1200px) {
					@include myFont ('Kyiv', normal, 500, 90px, 133%, #F5F5F5);
					margin-top: 415px;
					margin-bottom: 125px;
					margin-left: 143px;
					max-width: 746px;

			}
	}
}

