.block7 {
	display: flex;
	flex-direction: column;
	background-color: #1E0C06;
	border-radius: 0px 42px 42px 0px;
	box-shadow: 8px 8px 24px 0px rgb(106, 108, 131);
	padding-left: 19px;
	padding-right: 35px;
	padding-bottom: 30px;
	padding-top: 45px;


	@media screen and (min-width:576px) {
			padding-left: 100px;
			padding-right: 35px;
			padding-bottom: 30px;
			padding-top: 45px;
			border-radius: 0px 42px 42px 0px;
	}

	@media screen and (min-width:768px) {
			padding-left: 140px;
			padding-right: 35px;
			padding-bottom: 30px;
			padding-top: 45px;
			border-radius: 0px 42px 42px 0px;
	}

	@media screen and (min-width:1200px) {
			flex-direction: row;
			padding-left: 240px;
			padding-right: 70px;
			padding-bottom: 30px;
			padding-top: 45px;
			border-radius: 0px 42px 42px 0px;
	}
}


.block7-margin {

	margin-top: 30px;
	margin-bottom: 65px;

	@media screen and (min-width:768px) {
		margin-top: 105px;
		margin-bottom: 210px;
	}

	@media screen and (min-width:1200px) {
			margin-top: 105px;
			margin-bottom: 210px;
	}
}

.block7-1 {

	h3 {
			@media screen and (min-width:1200px) {
					margin-top: 0px;
					margin-bottom: 70px;
			}
	}

	p {
			@media screen and (min-width:1200px) {
					margin: 0px;
					line-height: 42px;
			}
	}
}

.b7-about {
	margin-top: 0px;
	margin-bottom: 40px;

	@media screen and (min-width:768px) {
			margin-top: 0px;
			margin-bottom: 70px;
	}

	@media screen and (min-width:1200px) {
			margin-bottom: 70px;
	}

}


.block7-pic {

	flex-basis: 360px;
	flex-shrink: 0;
	flex-grow: 1;
	position: relative;


	@media screen and (min-width:1200px) {
			flex-basis: 485px;
			flex-shrink: 0;
			flex-grow: 1;
			position: relative;
	}
}

.block7-pic1 {
	position: absolute;
	width: 183px;
	left: 10px;
	top: 50px;
	border: 7px solid #1E0C06;
	border-radius: 14px;
	z-index: 1;

	@media screen and (min-width:576px) {}

	@media screen and (min-width:768px) {
			position: absolute;
			top: 65px;
			left: 50px;
			z-index: 1;
			border: 15px solid #1E0C06;
			border-radius: 60px;
			width: auto;
	}

	@media screen and (min-width:1200px) {
			position: absolute;
			top: 125px;
			right: 75px;
			left: initial;
			z-index: 1;
			border: 15px solid #1E0C06;
			border-radius: 60px;
	}

}

.block7-pic2 {
	width: 111px;
	position: absolute;
	top: 25px;
	left: 170px;

	@media screen and (min-width:768px) {
			position: absolute;
			top: 20px;
			left: 365px;
			width: auto;
	}

	@media screen and (min-width:1200px) {
			position: absolute;
			top: 20px;
			right: 0;
			left: initial;
	}
}

.block7-pic3 {
	width: 111px;
	position: absolute;
	top: 250px;
	left: 130px;

	@media screen and (min-width:768px) {
			position: absolute;
			top: initial;
			bottom: 0;
			left: 300px;
			width: auto;
	}

	@media screen and (min-width:1200px) {
			position: absolute;
			bottom: 0;
			right: 30px;
			left: initial;
	}
}
