header {
	display: flex;
	align-items: center;
}

.hdr-black {
	background-color: #1E0C06;
	border-radius: 0 0 30px 30px;
	box-shadow: 8px 8px 24px 0px rgb(106, 108, 131);
	padding-bottom: 20px;


	@media screen and (min-width:768px) {
		background-color: #1E0C06;
		border-radius: 0 0 79px 79px;
		padding-bottom: 50px;
	}
}


.header-tabs {
	display: flex;
	gap: 40px;
	margin-left: auto;
	margin-bottom: 0px;
	margin-top: 0px;

	@media screen and (min-width:1200px) {
			gap: 63px;
	}

	@media screen and (max-width:768px) {
			display: none;
	}

	a {
			margin-bottom: 0px;
			margin-top: 0px;
			text-decoration: none;
			@include myFont ('Montserrat', normal, 700, 20px, 24px, aliceblue);
	}

}

.header-menu {
	cursor: pointer;
	margin-left: auto;

	@media screen and (min-width:768px) {
			display: none;
	}
}

