footer{
	margin-top: auto;
}
.block9 {
	background: #222021;
	box-shadow: 8px 8px 24px 0px rgb(152, 154, 173);
	display: flex;
	flex-direction: column;
	justify-content: center;

	@media screen and (max-width: 768px) {
		align-items: center;

	}

	@media screen and (min-width: 768px) {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
}

.b9-txt {
	@media screen and (max-width: 768px) {
		display: flex;
		flex-direction: column-reverse;
	}
}

.b9-txt1 {
	@media screen and (max-width: 768px) {
		display: none;
	}

	&::before {
		content: url("../img/footer-phone-mob.svg");
		vertical-align: middle;
		margin-right: 10px;

		@media screen and (min-width: 1200px) {
			content: url("../img/footer-phone.svg");
			margin-right: 19px;
		}
	}
}

.b9-txt2 {
	display: none;

	@media screen and (min-width: 768px) {
		display: block;
	}

	&::before {
		content: url("../img/footer_location-mob.svg");
		vertical-align: middle;
		margin-right: 10px;

		@media screen and (min-width: 1200px) {
			content: url("../img/footer_location.svg");
			margin-right: 19px;
		}
	}
}

.b9-txt3 {
	display: block;

	@media screen and (min-width: 768px) {
		display: none;
	}

	@media screen and (max-width: 768px) {
		margin-top: 45px;
		margin-bottom: 17px;
	}

	&::before {
		content: url("../img/footer_location-mob.svg");
		vertical-align: middle;
		margin-right: 10px;

		@media screen and (min-width: 1200px) {
			content: url("../img/footer_location.svg");
			margin-right: 19px;
		}
	}
}

.b9-txt4 {
	text-align: center;

	@media screen and (min-width: 768px) {
		display: none;
	}

	@media screen and (max-width: 768px) {
		margin-top: 0 px;
		margin-bottom: 35px;
	}

	&::before {
		content: url("../img/footer-phone-mob.svg");
		vertical-align: middle;
		margin-right: 10px;

		@media screen and (min-width: 1200px) {
			content: url("../img/footer-phone.svg");
			margin-right: 19px;
		}
	}
}

.b9-priv2 {
	display: block;
	text-align: center;
	margin-bottom: 0;
	margin-top: 0;

	@media screen and (min-width: 768px) {
		display: none;
	}
}

.b9-priv1 {
	display: none;
	text-align: left;

	@media screen and (min-width: 768px) {
		display: block;
		margin-top: 30px;
		margin-bottom: 0;
	}

	@media screen and (min-width: 1200px) {
		display: block;
		margin-top: 60px;
		margin-bottom: 0;
	}
}

.p-margin{

	@media screen and (min-width: 768px) {
		margin-top: 20px;
		margin-bottom: 20px;
	}

	@media screen and (min-width: 1200px) {
		margin-top: 30px;
		margin-bottom: 30px;
	}
}
