
.b6-advantages {

	width: 305px;
	height: 80px;
	margin-top: 120px;
	margin-bottom: 46px;

	@media screen and (min-width:576px) {
			width: 305px;
			height: auto;
			margin-top: 200px;
			margin-bottom: 80px;
	}

	@media screen and (min-width:768px) {
			width: 650px;
			height: auto;
			margin-top: 200px;
			margin-bottom: 80px;
	}

	@media screen and (min-width:1200px) {
			width: 950px;
			height: 230px;
			margin-top: 330px;
			margin-bottom: 120px;
	}
}




.block6-1 {
	display: flex;
	flex-direction: column-reverse;
	text-align: center;

	@media screen and (min-width:1200px) {
			flex-direction: row;
			gap: 120px;
			justify-content: center;
	}

	img {
			max-width: 350px;

			@media screen and (min-width:576px) {
					width: 100%;
					max-width: 610px;
			}

			@media screen and (min-width:768px) {
					max-width: 610px;
					width: 100%;
			}

			@media screen and (min-width:1200px) {
					width: 100%;
			}
	}
}

.block6-1-txt {
	p {
			margin-bottom: 46px;
			text-align: left;
			margin: 0 auto;
			margin-bottom: 11px;
			display: block;
			max-width: 350px;


			@media screen and (min-width:576px) {
					margin-bottom: 46px;
					max-width: 600px;
			}

			@media screen and (min-width:768px) {
					margin-bottom: 46px;
					max-width: 600px;
			}

			@media screen and (min-width:1200px) {
					margin-bottom: 46px;
					width: 400px;
			}
	}

	& p:last-child {
			margin-bottom: 46px;

			@media screen and (min-width:1200px) {
					margin-bottom: 0;
			}
	}
}


.btn-recive {
	margin: 0 auto;
	display: block;
	width: 220px;
	height: 35px;
	margin-top: 46px;
	margin-bottom: 93px;

	@media screen and (min-width:768px) {
			@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
			margin-top: 120px;
			margin-bottom: 305px;
			width: 472px;
			height: 60px;
	}

	@media screen and (min-width:1200px) {
			@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
			margin-top: 120px;
			margin-bottom: 305px;
			width: 472px;
			height: 60px;
	}

}
