.block8-dnone{
	display: block;

	@media screen and (min-width:1200px) {
		display: none;

	}
}

.b8-title {
	display: block;
	text-align: left;
	margin-top: 0px;
	margin-bottom: 11px;

	@media screen and (min-width:576px) {
			text-align: center;

	}

	@media screen and (min-width:768px) {
			margin-bottom: 45px;
	}

	@media screen and (min-width:1200px) {
			margin-top: 0;
			margin-right: 90px;
			text-align: right;
			margin-bottom: 45px;
	}
}

.block8-1 {
	display: flex;
	flex-direction: column-reverse;
	justify-content: center;
	flex-shrink: 1;
	flex-grow: 0;
	width: 100%;

	@media screen and (min-width:1200px) {
			flex-direction: row;
			justify-content: space-between;
	}
}


.b8-form {
	display: flex;
	flex-direction: column;
}


.b8-txt {

	p {
			max-width: 324px;
			margin: 0 auto;
			display: block;
			margin-bottom: 46px;

			@media screen and (min-width:768px) {
					max-width: 600px;
					margin: 0 auto;
					display: block;
					margin-bottom: 46px;
			}

			@media screen and (min-width:1200px) {
					margin-right: 90px;
					margin-left: 0;
					margin-top: 0;
					margin-bottom: 120px;
					max-width: 500px;
					text-align: right;
					height: 126px;

			}
	}

	img {
			display: none;

			@media screen and (min-width:1200px) {
					display: block;
			}
	}
}


.input-1 {
	background-color: transparent;
	border: 2px solid #728BAD;
	border-radius: 42px;
	margin-bottom: 23px;
	width: 324px;
	height: 40px;
	margin: 0 auto;
	margin-bottom: 12px;

	@media screen and (min-width:768px) {
			width: 600px;
			height: 80px;
			margin-bottom: 23px;
			border: 4px solid #728BAD;
	}

	@media screen and (min-width:1200px) {
			width: 600px;
			height: 80px;

	}

	&::placeholder {
			font-size: 13px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: 500;
			vertical-align: middle;
			padding-left: 19px;


			@media screen and (min-width:768px) {
					font-size: 24px;
					padding-left: 19px;
			}

			@media screen and (min-width:1200px) {
					font-size: 30px;
					padding-left: 19px;
			}
	}
}


.input-2 {
	background-color: transparent;
	border: 2px solid #728BAD;
	margin-bottom: 23px;
	width: 324px;
	height: 134px;
	margin: 0 auto;
	margin-bottom: 12px;
	border-radius: 14px;

	@media screen and (min-width:768px) {
			width: 600px;
			height: 257px;
			margin-bottom: 23px;
			border: 4px solid #728BAD;
			border-radius: 42px;
	}

	@media screen and (min-width:1200px) {
			width: 600px;
			height: 257px;
			margin-bottom: 40px;
	}

	&::placeholder {
			font-size: 13px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: 500;
			position: absolute;
			top: 14px;
			margin-left: 19px;

			@media screen and (min-width:768px) {
					font-size: 24px;
					margin-left: 19px;
			}

			@media screen and (min-width:1200px) {
					font-size: 30px;
					margin-left: 19px;
					position: absolute;
					top: 16px;
			}
	}
}

.input-3 {
	width: 92px;
	height: 34px;
	margin-left: 0;
	margin-bottom: 93px;

	@media screen and (min-width:415px) {
			margin: 0 auto;
			margin-bottom: 93px;
	}

	@media screen and (min-width:576px) {
			width: 92px;
			height: 34px;
			margin: 0 auto;
			margin-bottom: 93px;
	}

	@media screen and (min-width:768px) {
			width: 216px;
			height: 58px;
			margin: 0 auto;
			margin-bottom: 93px;
	}

	@media screen and (min-width:1200px) {
			width: 216px;
			height: 58px;
			margin-left: 0;
			margin-bottom: 0;
	}
}
