
.block2 {
	position: relative;
	background-color: #1E0C06;
	border-radius: 14px 0px 0px 14px;
	box-shadow: 8px 8px 24px 0px rgb(106, 108, 131);
	display: flex;
	flex-direction: column;
	align-items: stretch;
	margin-left: auto;
	margin-top: 50px;
	padding-left: 16px;
	padding-right: 11px;
	padding-top: 40px;
	padding-bottom: 50px;

	@media screen and (min-width:768px) {
			margin-top: 85px;
			flex-direction: row;
			border-radius: 40px 0 0 40px;
	}

	@media screen and (min-width:1200px) {
			margin-top: 85px;
			margin-left: auto;
			padding-left: 50px;
			padding-right: 115px;
			padding-bottom: 60px;
			padding-top: 45px;
	}
}



.block2-txt {

	h1 {

			@include myFont ('Kyiv', normal, 500, 30px, 110%, #F5F5F5);
			margin: 0;

			@media screen and (min-width:768px) {
					@include myFont ('Kyiv', normal, 500, 60px, 115%, #F5F5F5);
					margin-bottom: 0;
			}

			@media screen and (min-width:1200px) {
					@include myFont ('Kyiv', normal, 500, 90px, 115%, #F5F5F5);
					margin-top: 30px;
					margin-bottom: 0;

			}

	}

	p {
			@include myFont ('Inter', normal, 500, 15px, 18px, #FFDBBB);
			margin-bottom: 20px;
			margin-top: 10px;
			max-width: 60%;

			@media screen and (min-width:768px) {
					@include myFont ('Inter', normal, 500, 24px, 32px, #FFDBBB);
					margin-bottom: 60px;
					margin-top: 20px;
					max-width: 100%;
			}

			@media screen and (min-width:1200px) {
					@include myFont ('Inter', normal, 400, 30px, 39px, #FFDBBB);
					margin-bottom: 60px;
					margin-top: 20px;
			}
	}

	button {

			background-color: #728BAD;
			border-radius: 42px;
			width: 85px;
			height: 20px;
			margin-bottom: 42px;
			@include myFont ('Inter', normal, 700, 15px, 18px, #F5F5F5);
			border: none;


			@media screen and (min-width:768px) {
					width: 170px;
					height: 45px;
					@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
					margin-bottom: 42px;
			}

			@media screen and (min-width:1200px) {
					@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
					width: 225px;
					height: 58px;
					margin-bottom: 100px;

			}
	}
}


.block2-border {
	border-bottom: 1px solid #F5F5F5;

	@media screen and (min-width:768px) {
			border-left: 1px solid #F5F5F5;
			border-bottom: none;
			margin-right: 20px;
			margin-left: 20px;

	}

	@media screen and (min-width:1200px) {
			border-left: 1px solid #F5F5F5;
			margin-right: 45px;
			margin-left: 45px;
	}
}


.block2-pic {

	position: absolute;
	margin-left: 20px;
	margin-top: 90px;

	@media screen and (min-width:576px) {
			position: absolute;
			margin-left: 125px;
			margin-top: 90px;

	}


	@media screen and (min-width:768px) {
			position: relative;
			margin-left: 0;
			margin-top: 0;

	}

	@media screen and (min-width:1200px) {}
}

.block2-pic2 {
	display: flex;

	@media screen and (min-width:1200px) {
			gap: 40px;
	}
}


.pic1 {
	display: flex;
	margin-left: 190px;

	@media screen and (min-width:576px) {
			margin-left: auto;
	}

	@media screen and (min-width:768px) {
			margin-left: auto;
	}

	@media screen and (min-width:1200px) {
			margin-top: 0px;
			margin-right: 0px;
	}
}

.pic2 {
	display: flex;
	margin-top: 25px;
	margin-right: 65px;

	@media screen and (min-width:768px) {
			margin-top: 60px;
			margin-right: 65px;
	}

	@media screen and (min-width:1200px) {
			margin-top: 70px;
			margin-right: 0px;
	}
}

.pic3 {
	position: relative;
	margin-top: 100px;

	@media screen and (min-width:768px) {
			margin-top: 150px;
	}

	@media screen and (min-width:1200px) {
			margin-top: 200px;
	}
}
