@font-face {
	font-family: "Kyiv";
	src: url("../fonts/roboto/KyivTypeSans-Regular-.woff") format("woff"),
		url("../fonts/roboto/KyivTypeSans-Regular-.woff2") format("woff2");
}

@mixin myFont ($ff, $fstl, $fw, $fs, $lh, $clr) {
	font-family: $ff;
	font-style: $fstl;
	font-weight: $fw;
	font-size: $fs;
	line-height: $lh;
	color: $clr;
}

.btn {
	background-color: #728BAD;
	border-radius: 42px;
	@include myFont ('Inter', normal, 700, 15px, 18px, #F5F5F5);
	border: none;

	@media screen and (min-width: 768px) {
		@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);

	}

	@media screen and (min-width: 1200px) {
		@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
		;

	}
}

* {
	box-sizing: border-box;
}

