html,
      body {
        position: relative;
        height: 100%;
      }

      .swiper {
        width: 100%;
        height: 100%;
      }

      .swiper-slide {
        text-align: center;
        font-size: 18px;


        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
        background-color: #222021;
      }

      .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        background-color: #222021;
      }

      .custom-bullet {
        width: 15px;
        height: 15px;
        border: 4px solid #728bad;
        border-radius: 100%;
        margin: 0 9px;
        display: inline-block;
        box-sizing: border-box;
        cursor: pointer;

        &.swiper-pagination-bullet-active{
          background: #d5dcee;
        }

        @media screen and (min-width:768px) {
          width: 26px;
          height: 26px;
        }
      }

      .swiper-pagination{
        position: initial;
        margin-top: 15px;
        width: 100%;
        @media screen and (min-width:768px) {
          margin-top: 26px
        }
      }

      .swiper-button-next, .swiper-button-prev{
       position: absolute;
        width: 15px;
        height: 15px;
        background-image: url('../img/arrow.svg');
        background-repeat: no-repeat;
        background-size: contain;
        &:after {
          content:"";
        }
        @media screen and (min-width:768px) {
          width: 50px;
          height: 50px;
        }

      }
      .swiper-button-next{
        right: 0;
        margin-right: 20px;
        @media screen and (min-width:768px) {
          margin-right: 30px;
        }
      }

      .swiper-button-prev{
        transform: rotateZ(180deg);
        left: 0;
        margin-left: 20px;
        @media screen and (min-width:768px) {
          margin-left: 30px;
        }
      }


