
.block4 {
    display: flex;
    justify-content: space-between;
    overflow-x: scroll;
    padding-bottom: 25px;
    margin-bottom: 120px;

    @media screen and (min-width:576px) {
        overflow: auto;
        padding-bottom: 5px;
        margin-bottom: 260px;

    }

    img {
        width: 90px;

        @media screen and (min-width:1200px) {
            width: auto;
        }
    }
}


.block4-nomargin {
	display: flex;
	justify-content: space-between;
	overflow-x: scroll;
	padding-bottom: 25px;
	margin-bottom: 0px;

	@media screen and (min-width:576px) {
			overflow: auto;
			padding-bottom: 5px;
			margin-bottom: 0px;

	}

	img {
			width: 90px;

			@media screen and (min-width:1200px) {
					width: auto;
			}
	}
}


.block4-1 {
    display: flex;
    flex-direction: column;
    width: auto;
    text-align: center;
    flex-shrink: 0;

    @media screen and (min-width:768px) {
        width: auto;
    }

    p {
        margin-top: 16px;
        margin-bottom: 20px;

        @media screen and (min-width:768px) {
            margin-top: 40px;
            margin-bottom: 40px;
        }

        @media screen and (min-width:1200px) {}

    }

    ul {
        margin: 0 auto;

        li {
            list-style: url('../img/bullet-y.svg');
            text-align: left;
            margin-bottom: 7px;

            @media screen and (min-width:768px) {
                list-style: url('../img/bullet-y-dsk.svg');
                margin-bottom: 20px;
            }

            @media screen and (min-width:1200px) {
                margin-bottom: 28px;
            }
        }

        li:last-child {
            margin-bottom: 0px;
            list-style: url('../img/bullet-n.svg');

            @media screen and (min-width:768px) {
                list-style: url('../img/bullet-n-dsk.svg');
            }

            @media screen and (min-width:1200px) {}
        }
    }
}

.buk-pic {
    margin-top: 30px;

    @media screen and (min-width:576px) {
        margin-top: 0px;
    }
}
