.block11-txt {
	text-align: center;

	h3 {
			margin-top: 45px;
			margin-bottom: 0px;
			@media screen and (min-width:1200px) {
					margin-top: 0px;
			}
	}

	p {
			@include myFont ('Inter', normal, 500, 15px, 18px, #FFDBBB);
			margin-bottom: 35px;
			margin-top: 12px;

			@media screen and (min-width:768px) {
					@include myFont ('Inter', normal, 500, 24px, 32px, #FFDBBB);
					margin-bottom: 35px;
					margin-top: 12px;
			}

			@media screen and (min-width:1200px) {
					@include myFont ('Inter', normal, 400, 30px, 39px, #FFDBBB);
					margin-bottom: 40px;
					margin-top: 40px;
			}
	}
}


.btn-404 {
	margin: 0 auto;
	display: block;
	width: 195px;
	height: 35px;
	@media screen and (min-width:768px) {
			@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
			width: 445px;
			height: 60px;
	}

	@media screen and (min-width:1200px) {
			@include myFont ('Inter', normal, 700, 30px, 36px, #F5F5F5);
			margin-top: 0px;
			margin-bottom: 0px;
			width: 445px;
			height: 60px;
	}
}

