.block10 {
	display: flex;
	flex-direction: column;
	gap: 30px;
	margin: 0 auto;
	text-align: center;
	@media screen and (min-width:1200px) {
			flex-direction: row;
			justify-content: space-between;
			align-items:flex-start;
	}
}

.b10-alltxt{
	text-align: left;
	@media screen and (min-width:576px) {
			text-align: center;
	 }

	@media screen and (min-width:1200px) {
			text-align: left;
	 }
}

.b10-contact{
	margin-top: 0;
	margin-bottom: 40px;
	@media screen and (min-width:1200px) {
		 margin-bottom: 120px;

	}
}


.b10-txt1 {
	@media screen and (max-width:768px) {

	}
	&::before {
			content: url('../img/footer-phone-mob.svg');
			vertical-align: middle;
			margin-right: 10px;

			@media screen and (min-width:1200px) {
					content: url('../img/footer-phone.svg');
					margin-right: 19px;
			}
	}
}

.b10-txt2 {
	@media screen and (min-width:768px) {

	}

	&::before {
			content: url('../img/footer_location-mob.svg');
			vertical-align: middle;
			margin-right: 10px;

			@media screen and (min-width:1200px) {
					content: url('../img/footer_location.svg');
					margin-right: 19px;
			}
	}
}

.b10-map{
	width: 100%;
	height: 230px;
	border-radius: 14px;
	max-width: 495px;

	@media screen and (min-width:768px) {

	height: 428px;
	border-radius: 42px;
	}
}

.div-map {

	@media screen and (min-width:1200px) {
		max-width: 500px;
		width: 100%;
		}
}
